nav .links .links-separator,.mobile-toolbar button .button-line{background-color:rgba(0,0,0,0.2);height:1px}nav .links .nav-link{color:black;text-decoration:none}nav .links .links-separator,.mobile-toolbar button .button-line{background-color:rgba(0,0,0,0.2);height:1px}nav{position:fixed;left:0;top:0;max-width:280px;height:100vh;box-sizing:border-box;padding:20px;padding-right:0 !important;z-index:400;-webkit-transform:translateX(0);transform:translateX(0);-webkit-transition:-webkit-transform 0.3s ease;transition:-webkit-transform 0.3s ease;transition:transform 0.3s ease;transition:transform 0.3s ease, -webkit-transform 0.3s ease;background-color:white}nav.closed{-webkit-transform:translateX(-100%);transform:translateX(-100%)}@media (max-width: 700px){nav{padding:10px;max-width:100vw}}nav .logo-box{box-sizing:border-box;padding:50px 20px 0 50px;height:30%}nav .logo-box .logo{box-sizing:border-box;object-fit:cover;display:block;border:1px solid rgba(0,0,0,0.2);max-width:100%;max-height:100%}@media (max-width: 700px){nav .logo-box{display:none}}nav .links{box-sizing:border-box;padding-top:50px;height:55%;max-height:300px;display:flex;flex-direction:column;align-items:flex-start}@media (max-width: 700px){nav .links{margin-top:60px;padding-top:0;padding-right:100px}}nav .links .links-separator{-webkit-transition:width 0.4s ease;transition:width 0.4s ease}nav .links .nav-link{display:flex;align-items:center;flex-grow:1}nav .links .nav-link .link-text{font-size:25px;text-align:start;padding-left:50px}@media (max-height: 700px) and (min-width: 700px){nav .links .nav-link .link-text{font-size:20px;padding-left:40px}}nav .social-box{position:absolute;width:100%;height:15%;max-height:105px;bottom:0;left:0}nav .social-box .socials{margin-left:20px}@media (max-width: 700px){nav .social-box .socials{margin-left:10px}}nav .social-box .socials .social-icon{padding:10px;-webkit-transition:-webkit-transform 0.3s ease;transition:-webkit-transform 0.3s ease;transition:transform 0.3s ease;transition:transform 0.3s ease, -webkit-transform 0.3s ease}nav .social-box .socials .social-icon:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.backdrop{position:fixed;z-index:-1;background-color:black;opacity:0;top:0;left:0;width:0;height:0;-webkit-transition:opacity 0.3s ease;transition:opacity 0.3s ease}.backdrop.open{z-index:390;opacity:0.2;width:100vw;height:100vh}.mobile-toolbar{z-index:370;position:fixed;display:none;box-sizing:border-box;height:60px;left:0;right:0;margin:20px;background-color:white;border-bottom:1px solid lightgray;box-shadow:0 1px 10px rgba(0,0,0,0.25)}@media (max-width: 700px){.mobile-toolbar{display:flex;justify-content:space-between;align-items:center;margin:10px}}.mobile-toolbar button{display:block;height:30px;width:25px;float:left;border:none;background-color:#fff;display:flex;flex-direction:column;justify-content:space-evenly;align-items:stretch}.mobile-toolbar .mobile-logo{box-sizing:border-box;float:right;margin-right:5px;padding-bottom:5px;height:100%}.mobile-toolbar .mobile-logo img{height:100%}

