@import "variables";

.gallery {
  padding: $thumbnails-spacing-large/2;

  @media (max-width: 700px) {
    padding: 0;
  }

  .thumbnails-column {
    box-sizing: border-box;
    height: 100%;
    width: 33.33%;
    display: inline-block;
    vertical-align: top;
    @media (max-width: 1350px) {
      width: 50%;
    }
  }

  .inprogress-info {
    font-size: 22px;
    color: lightgray;
  }
}
